import { useSelector } from "react-redux"
import { Breadcrumb } from "../../components"
import { ConstTranslate } from "../../constants/translate"
import ContactInfo from "../../components/Contact/ContactInfo"
import { utilService } from "../../services/util.service"
import { ConstRoutes } from "../../constants/routes"


const AgentMessages = () => {
    const { currentUser } = useSelector(state => state.user)

    return (
        <div className="body-wrapper backgrounds__white-top space-pb--120">
            <Breadcrumb pageTitle={ConstTranslate.COMMUNICATE_WITH_AGENT} />
            {
                currentUser.agentMessages.map(message => {
                    return (
                        <div className="list-product border-bottom--medium">
                            <ContactInfo contact={message.contact} url={ConstRoutes.AGENT_MESSAGES_DETAILS_URL} />

                            <div className="list-product__price gap--15">
                                <h2>{message.contact.price}$</h2>
                                <p>{utilService.getFormattedDate(new Date(message.createdAt))}</p>
                            </div>
                        </div>
                    )
                })
            }
        </div>
    )
}


export default AgentMessages